.page-header {
  display: flex;
  padding: 22px 0px;
}

.page-header__body {
  margin-left: auto;
  margin-right: auto;
}

.page-header__logo-link {
  display: flex;
}

.burger-button {
  position: relative;
  right: 30px;
}

.page-header__menu-list.hidden {
  display: none;

  position: absolute;
  top: 79px;
  right: 560px;
  // right: 0px;
  // если смотреть в экране 320px
  z-index: 3;
  background-color: #ffffff;
}

.page-header__menu-item {
  display: flex;
  box-sizing: border-box;
  padding: 30px 0;
  padding-left: 75px;
  padding-right: 65px;
  width: 320px;
  box-shadow: 0px 1px 0 0 #ececec, inset 0px 1px 0 0 #ececec;
}

.page-header__menu-item:nth-child(3),
.page-header__menu-item:nth-child(4) {
  padding-left: 30px;
}

.page-header__menu-link {
  display: flex;
  align-items: center;
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
  text-align: right;
}

.page-header__menu-link:hover,
.page-header__menu-link:focus {
  color: rgba(0, 0, 0, 0.6);
}

.page-header__menu-link:active {
  color: rgba(0, 0, 0, 0.3);
}

.page-header__menu-icon {
  margin-right: 25px;
}

.page-header__menu-icon {
  fill: #cccccc;
}

.page-header__menu-icon:hover,
.page-header__menu-icon:focus {
  fill: #63d1bb;
}

.page-header__menu-icon:active {
  fill: #63d1bb;
  fill-opacity: 0.2;
}
